import React, { useState } from "react"
import { useStaticQuery, graphql } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import MenuItem from "../../components/menuitem.js"

import { Icon, Row, Col } from "react-materialize"

const data = [
  {
    title: "Steamed Soybeans",
    price: 5,
  },
]

export const menuImage = graphql`
  fragment menuImage on File {
    childImageSharp {
      fluid(maxWidth: 360, maxHeight: 240) {
        ...GatsbyImageSharpFluid
      }
    }
  }
`

const Noodle_Menu_Page = () => {
  const images = useStaticQuery(graphql`
    query {
      cold_soba: file(relativePath: { eq: "noodles/soba/cold_soba.jpg" }) {
        ...menuImage
      }
      zaru_soba: file(relativePath: { eq: "noodles/soba/zaru_soba.png" }) {
        ...menuImage
      }
      tempura_soba: file(
        relativePath: { eq: "noodles/soba/tempura_soba.png" }
      ) {
        ...menuImage
      }
      beef_soba: file(relativePath: { eq: "noodles/soba/beef_soba.png" }) {
        ...menuImage
      }
      cold_udon: file(relativePath: { eq: "noodles/udon/cold_udon.jpg" }) {
        ...menuImage
      }
      curry_udon: file(relativePath: { eq: "noodles/udon/curry_udon.jpg" }) {
        ...menuImage
      }
      beef_udon: file(relativePath: { eq: "noodles/udon/beef_udon.jpg" }) {
        ...menuImage
      }
      tempura_udon: file(
        relativePath: { eq: "noodles/udon/tempura_udon.jpg" }
      ) {
        ...menuImage
      }
      super_spicy_ramen: file(
        relativePath: { eq: "noodles/ramen/super_spicy_ramen.png" }
      ) {
        ...menuImage
      }
      kaishen_ramen: file(
        relativePath: { eq: "noodles/ramen/kaishen_ramen.png" }
      ) {
        ...menuImage
      }
      tsukemen: file(relativePath: { eq: "noodles/ramen/tsukemen.png" }) {
        ...menuImage
      }
      chilled_chinese_noodles: file(
        relativePath: { eq: "noodles/ramen/chilled_chinese_noodles.png" }
      ) {
        ...menuImage
      }
    }
  `)

  const [numTotalItems, setItems] = useState(0)

  const addItem = () => {
    setItems(numTotalItems + 1)
  }

  const removeItem = () => {
    setItems(numTotalItems - 1)
  }

  return (
    <Layout>
      <SEO title="Noodles | Online Ordering" />
      <div style={{ marginTop: "4%", marginLeft: "10%", width: "80%" }}>
        <Row>
          <Col s={3}>
            <MenuItem
              key={"1"}
              title="Cold Soba (Cold)"
              price={13.5}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_soba.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"2"}
              title="Zaru-Soba (Cold)"
              price={13}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.zaru_soba.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"3"}
              title="Tempura Soba (Hot or Cold)"
              price={16}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tempura_soba.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"4"}
              title="Beef Soba (Hot or Cold)"
              price={16}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.beef_soba.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"5"}
              title="Cold Udon (Cold)"
              price={12}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.cold_udon.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"6"}
              title="Curry Udon (Hot)"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.curry_udon.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"7"}
              title="Tempura Udon (Hot or Cold)"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tempura_udon.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"8"}
              title="Beef Udon (Hot or Cold)"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.beef_udon.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"9"}
              title="Tempura Inaniwa Udon (Hot)"
              price={25}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tempura_udon.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"10"}
              title="Beef Inaniwa Udon(Hot)"
              price={25}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.beef_udon.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"11"}
              title="Super Spicy Ramen"
              price={15}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.super_spicy_ramen.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"12"}
              title="Kaishen Ramen"
              price={18}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.kaishen_ramen.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"13"}
              title="Chilled Chinese Noodles"
              price={13}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.chilled_chinese_noodles.childImageSharp.fluid}
            />
          </Col>
          <Col s={3}>
            <MenuItem
              key={"14"}
              title="Tsukemen"
              price={18}
              addItem={addItem}
              removeItem={removeItem}
              picture={images.tsukemen.childImageSharp.fluid}
            />
          </Col>
        </Row>
      </div>
    </Layout>
  )
}

export default Noodle_Menu_Page
